import myformCard from "../../../components/myformCard.vue"
import popup from "../../../components/popup.vue"
import mulcheck from "@/components/mulcheck.vue";
export default {
	data() {
		return {
			//显示或者选中的变量
			checked: {
				first: true,
				second: false,
				thrid: false,
				four: false
			},
			revealChecked: {
				first: false,
				second: true,
				thrid: true
			},
			//各个状态的数量
			num:[0,0,0],
			//时间戳
			sjc: '',
			//
			jclx: '',
			wtrq: '',
			//草稿箱的表单卡
			actlistCao: [],
			//待结算的表单卡
			actlistDai: [],
			//已结算的表单卡
			actlistYi: [],
			//表单卡的状态
			state: '',
			//表单卡头标的颜色
			titleColor: '',
			tsObj: [], //提示选择人
			tsSectArr: [], //已选择人
			message:'2'
		}
	},
	components: {
		myformCard,
		popup,
		mulcheck
	},
	
	//加载时获取order页面传递的sjc，根据时间戳获取送检委托所有的表单数据
	onLoad: function(option) {
		this.sjc = option.sjc;
		this.wtrq = option.wtrq;
		this.$store.state.process='收样';
		uni.request({
			url: this.apiServer + '/account_regulate/accredit.php',
			data: {
				unit: '试验部'
			},
			method: 'POST',
			dataType: 'json',
			header: {
				'content-type': 'application/x-www-form-urlencoded', //POST头信息
			},
			success: (res) => {
				console.log(res.data);
				res = res.data;
				if (res[res.length - 1].result == "success") {
					res.splice(res.length - 1, 1);
					this.tsObj = res;
					console.log(res);
				} else {
					console.log('无授权数据');
				}
			},
			fail: (error) => {
				console.log(error);
			}
		});
		
	},
	methods: {
		open(e) {
			this.$refs.mulcheck.openPopup(e);
		},
		getVal([val, zt]) {
			var type=this.message;
			var peo=val;
			uni.request({
				url: this.apiServer + '/basicdata/components/message.php',
				data: {
					flag:"insert",
					type:type,
					peo:peo,
					wtbh:this.$store.state.wtbh,
					gcmc:this.$store.state.projectName
				},
				method: 'POST',
				dataType: 'json',
				header: {
					'content-type': 'application/x-www-form-urlencoded', //POST头信息
				},
				success: (res) => {
					console.log(res.data);
					if(res.data.result=="success"){
						var point = this;
						uni.sendSocketMessage({
							data: point.message+'|'+val+'|'+this.$store.state.projectName+'|'+this.$store.state.wtbh
						})
					}
				},
				fail: (error) => {
					console.log(error);
				}
			});
		},
		//三个状态按钮选中的切换
		replace(e) {
			if (e == 1) {
				this.checked = {
						first: true,
						second: false,
						thrid: false,
						four: false
					},
					this.revealChecked = {
						first: false,
						second: true,
						thrid: true,
					}

			} else if (e == 2) {
				// this.open(1);
				this.checked = {
						first: false,
						second: true,
						thrid: false,
						four: true
					},
					this.revealChecked = {
						first: true,
						second: false,
						thrid: true,
					}

			} else {
				this.checked = {
						first: false,
						second: false,
						thrid: true,
						four: true
					},
					this.revealChecked = {
						first: true,
						second: true,
						thrid: false,
					}
			}
		},
		//由myformCard子组件长按选项卡触发的父组件事件，这个事件触发的是popup子组件的openPopup方法
		openPopupAff([id, state]) {
			console.log(2222);
			this.$refs.popup.openPopup([id, state]);
		},
		reload([id, state, e]) {
			console.log(state);
			switch (state) {
				case 'a2&b':
					for (var i = 0; i < this.actlistCao.length; i++) {
						if (this.actlistCao[i].id == id) {
							console.log(this.actlistCao[i]);
							console.log(this.actlistCao[i].titleColor);
							if (e == '1') {
								this.actlistCao[i]['titleColor'] = 'g';
								this.actlistCao[i]['rwzt'] = '试验';
								this.actlistCao[i]['state'] = 'a3&g';
								this.actlistDai.push(this.actlistCao[i]);
								this.jiaZai();
								this.open(1);
							} else {
								this.actlistCao[i]['titleColor'] = 'b';
								this.actlistCao[i]['rwzt'] = '延期';
								this.actlistCao[i]['state'] = 'a4&b';
								this.actlistYi.push(this.actlistCao[i]);
								this.actlistCao.splice(i, 1);
								this.num[2]++;
								this.num[0]--;
							}
							
						}
					}
					break;
				case 'a4&b':
					for (var i = 0; i < this.actlistYi.length; i++) {
						if (this.actlistYi[i].id == id) {
							console.log(this.actlistYi[i]);
							console.log(this.actlistYi[i].titleColor);
							if (e == '1') {
								this.jiaZai();
							} else {
								this.actlistYi[i]['titleColor'] = 'r';
								this.actlistYi[i]['rwzt'] = '二次延期';
								this.actlistYi[i]['state'] = 'a4&r';
							}
						}
					}
					break;
				case 'a4&r':
					for (var i = 0; i < this.actlistYi.length; i++) {
						if (this.actlistYi[i].id == id) {
							this.actlistYi[i]['titleColor'] = 'g';
							this.actlistYi[i]['rwzt'] = '试验';
							this.actlistYi[i]['state'] = 'a3&g';
							this.actlistDai.push(this.actlistYi[i]);
							this.actlistYi.splice(i, 1);
							this.jiaZai();
						}
					}
			}
		},
		jiaZai(){
			this.actlistCao=[];
			this.actlistDai=[];
			this.actlistYi=[];
			this.num=[0,0,0];
			uni.request({
				url: this.apiServer + '/basicdata/getmyformcard.php',
				data: {
					flag: 'received',
					sjc: this.sjc,
					wtrq: this.wtrq,
					jccs:this.$store.state.jccs
				},
				method: 'POST',
				dataType: 'json',
				header: {
					'content-type': 'application/x-www-form-urlencoded', //POST头信息
				},
				success: (res) => {
					res = res.data;
					console.log(res);
					this.jclx = res[0].jclx;
					for (var i = 0; i < res.length; i++) {
						this.state = (res[i].state || ' ').split('&')[0];
						this.titleColor = (res[i].state || ' ').split('&')[1];
			
						if (this.state == 'a3') {
							res[i]['firstTitle'] = '检测类型';
							res[i]['secondTitle'] = '试验开始日期';
							res[i]['thridTitle'] = '试验完成日期';
							res[i]['fourTitle'] = '试验结论';
						} else {
							res[i]['firstTitle'] = '检测类型';
							res[i]['secondTitle'] = '计划开始日期';
							res[i]['thridTitle'] = '计划完成日期';
							res[i]['fourTitle'] = '任务状态';
						}
						if (this.state == 'a2') {
							this.num[0]++;
							res[i]['titleColor'] = this.titleColor;
							this.actlistCao.push(res[i]);
						} else if (this.state == 'a3') {
							this.num[1]++;
							res[i]['titleColor'] = this.titleColor;
							this.actlistDai.push(res[i]);
						} else if (this.state == 'a4') {
							this.num[2]++;
							res[i]['titleColor'] = this.titleColor;
							this.actlistYi.push(res[i]);
						} else {
			
						}
					}
				},
				fail: (error) => {
					console.log(error);
				}
			});
		}

	},
	//页面加载或页面返回初始化状态
	onShow: function() {
		this.reveal = {
			first: true,
			second: false,
		},
		this.jiaZai();
	}
}
